<template>
  <div class="container attend-detail">
    <van-nav-bar
      fixed
      left-text="返回"
      @click-left="onClickLeft"
      left-arrow>
    </van-nav-bar>
    <div class="attend-detail-item">
      <div>主题</div>
      <div>{{form.theme}}</div>
    </div>
    <div class="attend-detail-item">
      <div>会议类型</div>
      <div>{{form.type}}</div>
    </div>
    <div class="attend-detail-item">
      <div>组织者</div>
      <div>{{form.organizer}}</div>
    </div>
    <div class="attend-detail-item">
      <div>主办部门</div>
      <div>{{form.dept}}</div>
    </div>
    <div class="attend-detail-item">
      <div>会议室</div>
      <div>{{form.room}}</div>
    </div>
    <div class="attend-detail-item">
      <div>开始时间</div>
      <div>{{form.start}}</div>
    </div>
    <div class="attend-detail-item">
      <div>结束时间</div>
      <div>{{form.end}}</div>
    </div>
    <div class="attend-detail-item">
      <div>参会人员</div>
      <div>{{form.participant}}</div>
    </div>
  </div>
</template>

<script>
import { NavBar } from 'vant'
export default {
  components: {
    [NavBar.name]: NavBar
  },
  data () {
    return {
      form: {
        theme: '',
        type: '',
        organizer: '',
        dept: '',
        room: '',
        start: '',
        end: '',
        participant: ''
      }
    }
  },
  created () {
    this.initData()
  },
  methods: {
    onClickLeft () {
      this.$router.push({
        path: '/manage/index',
        query: {}
      })
    },
    initData () {
      const data = {
        theme: 'jia的会议',
        type: '例会',
        organizer: '陈建',
        dept: '通信研究院',
        room: '13楼会议室',
        start: '2019-11-05 12:00',
        end: '2019-11-05 14:00',
        participant: '张三'
      }
      Object.keys(this.form).forEach(k => {
        this.form[k] = data[k]
      })
    }
  }
}
</script>

<style scoped>
.attend-detail-item{
  height: 50px;
  line-height: 50px;
  display: flex;
  justify-content: flex-start;
  margin-top: 5px;
  border-radius: 10px;
  background-color: #fff;
}
.attend-detail-item>div:first-child{
  width: 55%;
  padding-left: 10px;
}
.attend-detail-item>div:nth-child(2){
  width: 40%;
}
</style>



